<template>
	<view class="list_goods">
		<!-- 表格 -->
		<view class="goods_table">
			<view class="btn_change_table" @click="show_table()">
				{{ title }}
			</view>
			<view class="goods_table_block" v-show="is_opened">
				<uni-table stripe border emptyText="暂无更多数据">
					<uni-tr>
						<uni-th align="center">图片</uni-th>
						<uni-th align="center">标题</uni-th>
						<uni-th align="center">价格</uni-th>
						<uni-th align="center">原价</uni-th>
						<uni-th align="center">购物车</uni-th>
					</uni-tr>
					<uni-tr v-for="(o, i) in list" :key="i">
						<uni-td class="image" align="center">

							<image style="width:4rem;height: 4rem;" :src="$fullImgUrl(o[vm.img])" mode="scaleToFill">
							</image>
						</uni-td>
						<uni-td class="title" align="center">
							{{ o[vm.title] }}
						</uni-td>
						<uni-td class="price" align="center">
							￥{{ o[vm.price] }}
						</uni-td>
						<uni-td class="price_ago" align="center">￥{{ o[vm.price_ago] }}</uni-td>
						<uni-td class="cart" align="center">
							<uni-icons type="cart"></uni-icons>
						</uni-td>
					</uni-tr>
				</uni-table>
			</view>
		</view>
		<!-- /表格 -->
		<!-- 列表 -->
		<view class="goods_list">
			<view class="row" v-show="!is_opened">
				<view v-for="(o, i) in list" :key="i" class="col-6 col-md-3">
					<navigator class="goods_item" :url="'/pages/goods/details?goods_id=' + o[vm.goods_id]">
						<view class="media">
							<image :src="$fullImgUrl(o[vm.img])" mode="widthFix"></image>
						</view>
						<view class="bottom">
							<view class="title">
								{{ o[vm.title] }}
							</view>
							<view>
								<view class="price">
									￥{{ o[vm.price] }}
								</view>
								<view class="price_ago">
									￥{{ o[vm.price_ago] }}
								</view>
								<uni-icons class="icon_cart" type="cart" size="16" color="var(--color_primary)">
								</uni-icons>
							</view>
						</view>
					</navigator>
				</view>
			</view>
		</view>

	</view>

</template>

<script>
	export default {
		props: {
			url: {
				type: String,
				default: '/pages/goods/details?goods_id='
			},
			list: {
				type: Array,
				default: function() {
					return [];
				}
			},
			vm: {
				type: Object,
				default: function() {
					return {
						goods_id: "goods_id",
						img: "img",
						title: "title",
						price: "price",
						price_ago: "price_ago"
					}
				}
			},
			span: {
				type: Number,
				default: 2
			}
		},
		data() {
			var col = 12 / this.span;
			return {
				col,
				is_opened: false,
				title: '切换成表格'
			}

		},
		methods: {
			show_table() {
				this.is_opened = !this.is_opened
				if (this.is_opened === true) {
					this.title = '切换成列表'
				} else {
					this.title = '切换成表格'
				}
			}
		}
	}
</script>

<style scoped>
	.ellipsis_2 {
		overflow: hidden;
		text-overflow: ellipsis;
		display: -webkit-box;
		-webkit-line-clamp: 2;
		-webkit-box-orient: vertical;
	}

	.btn_change_table {
		font-weight: bold;
		padding: 0.5rem 0;
		width: 100px;
		font-size: 0.875rem;
	}

	.row {
		margin-left: -1rem;
		margin-right: -1rem;
	}

	.goods_item {
		padding: var(--padding_base);
	}

	.title {
		overflow: hidden;
		white-space: nowrap;
		text-overflow: ellipsis;
		margin-bottom: var(--margin_small);
	}

	image {
		width: 100%;
	}

	.price {
		display: inline-block;
	}

	.price_ago {
		font-size: var(--font_mini);
		color: var(--color_grey);
		display: inline-block;
		text-decoration: line-through;
	}

	.icon_cart {
		float: right;
	}
</style>
